body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.w-100 {
    width: 14%!important;
}
.blue-text{
   color:Gold;
}
@-webkit-keyframes alert {
  25%{opacity:0.5}
  50%{opacity:1}
  75%{opacity:0.5}
  100%{opacity:0}  
}

@keyframes alert {
  25%{opacity:0.5}
  50%{opacity:1}
  75%{opacity:0.5}
  100%{opacity:0}  
}

.container{
  flex-basis: 65%;
  display: flex;flex-direction: column;
}
.above{display: flex;}
.bottom, .above{
  display: flex; justify-content: space-between ;  margin: 2%;
}
.botlft, .botrgt, .ablft, .abrgt{
  flex-basis: 50%; margin-right: 2%;
}
.ablft{display: flex;}
.submit{
  border: 2px solid;
  border-radius: 5px;  
  padding: 2% 4%; background-color: transparent; 
  align-self: flex-end;
  font-size: 120%; margin: 2%;
}
.submit:hover{background-color: green; -webkit-transition-duration: .3s; transition-duration: .3s;}
.male,.female{flex-basis: 48%; text-align: center;}
.male>img, .female>img{width: 50%;border: 3px solid;}
.qtt{font-size: 250%; color:rgb(190, 0, 20); text-shadow: 0px 0px 4px white; text-align: center; 
}
.DivBg{
  margin: 2%;
  background-color: rgba(200, 100, 20, 1);
  color: white;
  display: flex; /*justify-content: space-around;*/
  border-radius: 5px;
}
.Divleft{  
  text-align: center; margin-right: 4%;
  border-radius: 8px; display: flex; flex-direction: column; justify-content: center; flex-basis: 30%;
}
.img{background-color: white; border-radius: 90px; width: 200px; margin: 1%; display: flex; justify-content: center; align-self: center;}
.img img{padding-top: 4%;}
.logo{
  width: 37%;
    height: 81px;
}
.nav-link{
  font-size: 125%;
    font-weight: bold;
}
.proverbe{
  background-color: lightgray;
    height: 188px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

